.news-inner {
    padding: 112px 0 0;

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 239px;
        min-height: 384px;
        border-radius: 0 0 24px 24px;
        background-color: $white;
    }

    .story {
        display: flex;

        .sidebar {
            width: 100%;
            max-width: 240px;

            &__item {
                &:not(:last-child) {
                    margin-bottom: 18px;
                }
            }

            &__data {
                font-size: 14px;
                line-height: 20px;
                color: $grey-text;
            }

            &__name {
                font-weight: 700;
                line-height: 28px;
            }
        }

        &__wrapper {
            width: 100%;
            max-width: 912px;
        }

        &__inner {
            margin: 0 auto;
            max-width: 672px;

            p:not(:last-child) {
                margin-bottom: 32px;
            }
        }

        &__title {
            margin-bottom: 25px;
            font-size: 32px;
            line-height: 40px;
            letter-spacing: -0.02em;
        }

        &__img-wrapper {
            position: relative;
            margin: 55px 0 45px;
            width: 100%;
        }

        &__img {
            margin-bottom: 12px;
            border: 24px;
            object-fit: cover;
        }

        &__author {
            position: absolute;
            left: 16px;
            bottom: 40px;
            font-size: 12px;
            line-height: 16px;
            color: $white;
        }

        &__description {
            font-weight: 300;
            font-style: italic;
            font-size: 12px;
            line-height: 16px;
            text-align: center;
        }

        &__naming {
            margin: 62px 0 18px;
            font-size: 24px;
        }

        &__notice {
            margin-bottom: 48px;
            padding: 20px 20px 20px 24px;
            border-radius: 12px;
            color: $white;
            background-color: $black;
        }

        &__naming--small {
            margin: 0 0 9px;
            font-weight: 700;
            font-size: 20px;
        }

        &__quote-box {
            margin: 79px auto 76px;
            padding-left: 20px;
            border-left: 2px solid $green;
        }

        &__quote-title {
            margin-bottom: 11px;
            font-size: 32px;
            line-height: 40px;
        }

        &__quote-text {
            font-weight: 700;
            font-size: 12px;
            line-height: 16px;
        }
    }
}

.propose {
    display: flex;
    justify-content: space-between;
    margin-bottom: 136px;
    width: 100%;
    border-radius: 24px;
    background: #fff;

    &__img-wrapper {
        width: 100%;
        max-width: 520px;
        border-radius: 24px;
    }

    &__img {
        height: 100%;
        border-radius: 24px;
        object-fit: cover;
    }

    &__info {
        margin: 0 24px;
        padding: 171px 0 138px;
        width: 100%;
    }

    &__descr {
        margin: 0 auto;
        max-width: 591px;
    }

    &__topic {
        text-transform: uppercase;
    }

    &__title {
        max-width: 100%;
        color: $black;
    }

    &__subtitle {
        margin-bottom: 24px;
    }

    &__img-wrapper--bottom {
        max-width: 432px;
    }

    &__img--bottom {
        margin-top: -140px;
    }
}